import Vue from 'vue'
import vueRouter from 'vue-router'


import hellow from '../components/HelloWorld'
import ivew from '../components/ivew'
import content1 from '../components/content1'
import content2 from '../components/content2'
import ll from '../components/ll'
import ex from '../components/ex'
import elementui from '../components/elementui'
import elementuilink from '../components/elementuilink'
import printing from '../components/printing'
import slotIndex from '../components/slotIndex'
import testtable from '../components/testtable'
import parentTable from '../components/parentTable'
// import HelloWorld from '../components/HelloWorld'
import ceshiTable from '../components/el-table/parentTable'
Vue.use(vueRouter);


export default new vueRouter({

    routes:[
        // {
        //     path:'/',component:ll,name:'ll',
        //     // children:[
        //     //     {
        //     //         path:'ll',
        //     //         component:ll,
        //     //         name:'ll',
        //     //         meta:{title:'哈哈',id:4,icon:'ios-analytics-outline'}
        //     //
        //     //     }
        //     // ]
        //
        //
        // },
      {
          path:'/parentTable',component:parentTable,name:'parentTable',
          children:[
              {
                  path:'testtable',
                  component:testtable,
                  name:'testtable',

              }
          ]
      },
        {
            path:'/ceshiTable',component:ceshiTable,name:'parentTable',

        },
        {
            path:'/elementui',component:elementui,name:'elementui',
            children:[
                {
                    path:'ll',
                    component:ll,
                    name:'ll',
                    meta:{title:'哈哈',id:4,icon:'ios-analytics-outline'}

                },
                {
                    // path:'elementuilink/:userId',
                    path:'elementuilink',
                    component:elementuilink,
                    name:'elementuilink',
                    meta:{title:'哈哈',id:4,icon:'ios-analytics-outline'}

                }
            ]


        },
        {
            path:'/hellow',component:hellow,name:'hellow',

        },
        {
            path:'/editer',
            component:()=>import('../components/editer/editer'),
            name:'editer',

        },
        {
            path:'/testScroll',
            component:()=>import('../components/testBetterScroll/testScroll'),
            name:'testScroll',

        },
        {
            path:'/mobile-left',
            component:()=>import('../components/mobile-left-right/mobile-left-right'),
            name:'mobile-left-right',

        },
        {
            path:'/printing',component:printing,name:'printing',

        },
        {
            path:'/jj',
            component:()=>import('../components/jj'),
            name:'jj',
            children:[
                {
                    path:'ll',
                    component:ll,
                    name:'ll',
                    meta:{title:'哈哈',id:3,icon:'ios-analytics-outline'},
                    children:[
                        {
                            path:'ex',
                            component:ex,
                            name:'ex',
                            meta:{title:'哈哈',id:4,icon:'ios-analytics-outline'}

                        }
                    ]

                }
            ]



        },
        {
            path:'/testOss',
            component:()=>import('../components/testOss'),
            name:'testOss',
        },
        // slot实例以及父子组件传值
        {
            path:'/slotIndex',
            component:()=>import('../components/slotIndex'),
            name:'slotIndex',
        },
        // {
        //     path:'/pl-index',
        //     component:()=>import('../components/pl-table/components-pl-table'),
        //     name:'components-pl-table',
        // },
        // {
        //     path:'/pl-index',
        //     component:()=>import('../components/pl-table/parent-pl-table'),
        //     name:'parent-pl-table',
        // },
        {
            path:'/pl',
            component:()=>import('../components/pl-table/parent-pl-table'),
            name:'parent-pl-table',
        },
        {
            path:'/ivew',component: ivew,name:'ivew',
            meta:{
                title:'列表',

            },
            children:[
                {
                    path:'content1',
                    component:content1,
                    name:'content1',
                    meta:{title:'哈哈',id:1,icon:'ios-analytics-outline'}

                },
                {
                    path:'content2',
                    component:content2,
                    name:'content2',
                    meta:{title:'嘿嘿',id:2,icon:'logo-android'}
                },
            ]
        }
    ]
})
