<template>
    <div>
        <testtable ref="parentTable" @row-action="actionFn"  @sort-customs="sort" @page-change="pageChange"></testtable>
    </div>

</template>

<script>
  import testtable from './testtable.vue';
  export default {
    name: "parentTable",
    data(){
      return{
        tableBoty:[{
          id: '12987122',
          name: '王小虎',
          amount1: 6,
          amount2: 'gh-3.2',
          amount3: 10
        }, {
          id: '12987123',
          name: '王小虎',
          amount1: 2,
          amount2: 'gh-4.43',
          amount3: 12
        }, {
          id: '12987124',
          name: '王小虎',
          amount1: 3,
          amount2: '1.9',
          amount3: 9
        }, {
          id: '12987125',
          name: '王小虎',
          amount1: 4,
          amount2: 'gh-2.2',
          amount3: 17
        }, {
          id: '12987126',
          name: '王小虎',
          amount1: 5,
          amount2: 'gh-4.1',
          amount3: 15
        }],
        columnData:[
          {
            title: "ID",
            prop: "id", //后台返回的key
            sortAbled: false, //能否排序
            sortOrder: "none", //按照哪种方式排序
            search:'id'
          },
          {
            title: "姓名",
            prop: "name", //后台返回的key
            sortAbled: false, //能否排序
            search:'name'
          },
          {
            title: "数量1",
            prop: "amount1", //后台返回的key
            // sortAbled: 'custom', //能否排序
            // search:'amount1'
          },
          {
            title: "数量2",
            prop: "amount2", //后台返回的key
            // sortAbled: true, //能否排序
            // search:'amount2'
          },
          {
            title: "数量3",
            prop: "amount3", //后台返回的key
            // sortAbled: true, //能否排序
            // search:'amount3'
          },
        ],
        action:[{name:'添加',type:'danger',id:1},{name:'删除',type:'danger',id:2}]
      }
    },
    components:{
      testtable
    },
    mounted(){
      var that = this;
      that.$refs.parentTable.renderTableBody(that.tableBoty);
      that.$refs.parentTable.renderColumn(that.columnData);
      that.$refs.parentTable.renderAction(that.action);
    },
    created:function(){

    },
    methods:{
      actionFn(index,row,name){
        console.log('op')
      },

      sort(colums){
        var that = this;
        if(colums.order == "ascending"){
          that.tableBoty.sort(that.arrObjSort_down(colums.prop));
        }else if(colums.order == "descending"){
          that.tableBoty.sort(that.arrObjSort_up(colums.prop));
        }
        that.$refs.parentTable.renderTableBody(that.tableBoty);

      },
      arrObjSort_down(protoTypeName){
        return function(object1,object2){
          var val1 = object1[protoTypeName];
          var val2 = object2[protoTypeName];
          if(val1<val2){
            return -1;
          }else if(val1>val2){
            return 1;
          }else{ return 0;
          }
        }
        },
      arrObjSort_up(protoTypeName){
        return function(object1,object2){
          var val1 = object1[protoTypeName];
          var val2 = object2[protoTypeName];
          if(val1>val2){
            return -1;
          }else if(val1<val2){
            return 1;
          }else{ return 0;
          }
        }
      },
      pageChange(val){
        console.log(val)

      }
    }
  }
</script>

