<template>
    <div>
        <el-table
           :data="tablebodyData"
           border
           @sort-change="sort_fn"
           style="width: 100%">
            <el-table-column type="selection" width="40" align="center" />
            <el-table-column
                    v-for="(column,index) in tableColumn"
                    :prop="column.prop"
                    :sortable="column.sortAbled"
                    :sort-by="column.prop">
                <template slot="header" slot-scope="scope">
                      <span>{{ column.title }}</span>
                      <div class="searchinput" v-if="column.sortAbled"  @click.stop="clickStop">
                          <el-input
                                  v-model="search"
                                  size="mini"
                                  placeholder="输入关键字搜索"
                                  @input="search_fn(column.search)"
                          />
                      </div>
                </template>
            </el-table-column>
            <el-table-column
                    align="right"
                    label="操作">
                <template slot-scope="scope">
                    <el-button size="mini" v-for="(action,index) in tableAction"  @click="handleAction(scope.$index, scope.row,action.name)">{{ action.name }}</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                @current-change="handleCurrentChange"
                background
                layout="prev, pager, next"
                :total="30">
        </el-pagination>
    </div>
</template>

<script>
  export default {
    name: "testtable",
    data() {
      return {
        search:'',
        filterValue:'',
        tableData:'',
        sortColumn:'',
        tableColumn:'',
        tableAction:'',
      };
    },
    computed:{
      tablebodyData(){
        var that = this;
        if(that.tableData){
          return that.tableData.filter((data) => {
            return  !that.search || data[that.filterValue] == that.search
          });
        }
      }
    },
    methods: {
      renderTableBody(data){
        this.tableData = data;
      },
      renderColumn(data){
        this.tableColumn = data;
      },
      renderAction(data){
        this.tableAction = data;
      },
      clickStop(){
      },
      handleAction(index,row,actionName){
        this.$emit("row-action",index,row,actionName)
      },
      sortByDate(obj1, obj2) {
        console.log('op')
        let val1 = obj1[this.sortColumn.prop];
        let val2 =  obj2[this.sortColumn.prop];
        return val2-val1
      },
      search_fn(filter){
        var that = this;
        that.filterValue = filter
      },
      sort_fn(column, prop, order){
        this.$emit("sort-customs",column)
      },
      handleCurrentChange(val){
        this.$emit("page-change",val)

      }
    },


  };
</script>
<style scoped>
    .el-table .cell{
        position: relative;
        overflow: auto;
    }
    .el-table-column .caret-wrapper{
        position: absolute!important;
        top: 0;
        right: 0;

    }
    .kk{
        color: red;
    }
    .searchinput{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
    }
    .el-pagination{
        margin-top: 10px;
    }


</style>
<style>
    .el-table .cell{
        position: relative;
        min-height: 70px;
        text-align: center;
    }
    .el-table .caret-wrapper{
        top: 0;
        right: 0;

    }
    .searchinput input{
        border: 0;
        text-align: center;


    }
</style>