import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
import train from './controller/train'
import newcon from './controller/newcon'
console.log(newcon)


function aa() {
    var a = 0;
    setTimeout(function () {
        a = a+1;
    },1000);

    return a;
}

const store = new Vuex.Store({
    state:{
        info:'kkkk'
    },
    getters:{
      getInfo(state){
          return state.info+'popollkkkjjjj'
      },
      sidebar: state => state.train.trainstate,
      changeList:state=> state.train.list,
      computedinfo(state){
          return state.info.replace('k')
      },
      getnewstate(state){
        return state.newcon.newstate
      }
    },
    mutations:{
        increment (state,ceshivalue) {
            setTimeout(function () {
                // console.log('1')
            },1000)
            // 变更状态
            state.info = state.info+'llll'
            ceshivalue = ceshivalue+1;
            console.log(ceshivalue)

        },
      change_mutation(state,num){
            state.info = '变了'

      }
    },
    actions:{
        async ceshiactions(store,payload){
            // console.log('后执行')
            let data = await aa();
            // console.log(data)

        },
    },
    modules:{
        train,
        newcon
   },



});
export default store
