<template>

        <div >
            <!--<pl-table :datas="tableData" height="400" useVirtual
            注意点： 表格插件并不需要height属性字段(跟ele不同)，因为涉及分页组件。里面会去计算高度，只需要父级外层设置高度即可
            row-height属性很重要 请看表格属性列表
            -->
            <el-table :data="table"
                      stripe
                      ref="plTable"
                      big-data-checkbox
                      fixed-columns-roll
                      header-drag-style
                      :row-height="height"
                      @sort-change="sort_fn"
                      @selection-change="Select"
            style="width: 100%">
                <template slot="empty">
                    没有查询到符合条件的记录
                </template>
                <el-table-column type="selection" width="55" v-if="checkBox"/>
                <!--<pl-table-column type="index" width="100" />-->
                <!--show-overflow-tooltip属性代表超出则内容部分给出提示框-->
                <el-table-column
                        v-for="(item,index) in columns"
                        :key="item.id"
                        :resizable="item.resizable"
                        :show-overflow-tooltip="item.showOverflowTooltip"
                        :prop="item.prop"
                        :sortable="item.sortAbled"
                        :sort-by="item.prop"
                        :fixed="item.fixed"
                        :width="item.width">
                    <template slot="header" slot-scope="scope">
                        <span class="rowItem" >
                            <span v-if="item.searchAbled" @click.stop="clickStop">
                                <el-popover
                                        placement="right"
                                        ref="popover"
                                        title="根据关键词进行搜索"
                                        width="240"
                                        trigger="manual"
                                        v-model="item.searchPopoverVisibile"
                                >
                                <div>
                                     <el-input
                                             v-model="searchObj[item.prop]"
                                             style="width: 150px;margin-right: 5px"
                                             size="mini"
                                             placeholder="输入关键字搜索"
                                             clearable
                                             @input="handleInput(item.prop)"
                                     />
                                    <el-button size="mini" type="primary" @click="closePovover(index,item.prop)">关闭</el-button>
                                </div>

                                <i slot="reference" class="el-icon-search" @click="openPopoever(index,item.prop)"></i>
                            </el-popover>
                            </span>

                            {{ item.label }}
                        </span>

                    </template>
                    <template slot-scope="scope">
                        <slot :name="item.prop" v-if="item.scope" :rowdata="scope.row" :propdata="scope.row[item.prop]"></slot>
                        <span v-else> {{ scope.row[item.prop] }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                        align="right"
                        width="200"
                        label="操作"
                        v-if="actionNeed">
                    <template slot-scope="scope">
                        <slot name="action" :rowdata="scope" v-if="rowCustom"></slot>
                        <el-button size="mini" v-else :type="action.type" v-for="(action,index) in tableAction"  @click="handleAction(scope.$index, scope.row,action.name)">{{ action.name }}</el-button>
                    </template>
                </el-table-column>

            </el-table>
            <el-pagination
                    @size-change="changeSize"
                    @current-change="handleCurrentChange"
                    background
                    :current-page="page"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="size"
                    :total="30">
            </el-pagination>
        </div>
</template>

<script>
    export default {
        name: "components-el-table",
        data() {
            return {
                size:10,
                page:1

            }
        },
        props:{
            // 表格数据
            tableData:{
                type:Array,
                required: true
            },
            // 表头数据
            columns:{
                type:Array,
                required: true
            },
            // 操作数据（这个是取决与rowCustom为true得话就不需要传进来了）
            tableAction:{
                type:Array
            },
            // 是否要前面的选中框
            checkBox:{
                type:Boolean,
                default:false
            },
            // 如果传进来为true说明需要变化在外面写 如果没传就是直接循环tableation
            rowCustom:{
                type:Boolean,
                default: false
            },
            actionNeed:{
                type:Boolean,
                default: true
            },
            searchObj:{
                default:false
            },
            total:{
                type:Number,
            },
            pagination:{
                type:Boolean,
                default:true
            },
            height:{
                default:60
            }
        },
        computed:{
            table(){
                if(this.searchObj){
                    return this.renderSearchArr(this.tableData,this.searchObj)
                }else{
                    return this.tableData
                }

            },
        },
        methods: {
            renderSearchArr(data,filterObj){
                var temp = [];
                data.forEach((item,index)=>{
                    var flag = false
                    for (var i in filterObj){
                        if( item[i] == filterObj[i]||filterObj[i]==''){
                            flag = true;
                        }else{
                            flag = false;
                            break
                        }
                    }
                    if(flag){
                        temp.push(item)
                    }
                })
                return temp
            },
            sort_fn(column, prop, order){
                this.$emit("sort-customs",column)
            },
            closePovover(index,prop){
                this.searchObj[prop] = '';
                this.columns[index].searchPopoverVisibile = false
            },
            clickStop(){
            },
            openPopoever(index,prop){
                this.searchObj[prop] = '';
                this.columns[index].searchPopoverVisibile = true;
                // this.filterValue = prop
                // console.log(this.columns[index].searchPopoverVisibile)
            },
            handleInput(prop){
                console.log(this.searchObj)
                // console.log(this.test)
            },
            handleAction(index,row,name){
                this.$emit("row-action",index,row,name)
                // console.log(index,row,name)
            },
            Select(data){
                this.$emit("select",data)
                // console.log(data)
            },
            handClear(){
                this.search = ''
            },
            handlePage(page,size){
                this.columns.map((item)=>{
                    item.searchPopoverVisibile = false
                });
                for (var i in this.searchObj){
                    this.searchObj[i]=''
                }
                this.$emit("page-controll",page,size)

            },
            changeSize(val){
                this.size = val
                this.$emit("page-change",this.page,this.size)

            },
            handleCurrentChange(val){
                this.page = val
                this.$emit("page-change",this.page,this.size)

            }
        }
    }
</script>
<style scoped>
    .el-table .cell{
        position: relative;
        overflow: auto;
    }
    .el-table-column .caret-wrapper{
        position: absolute!important;
        top: 0;
        right: 0;

    }
    .kk{
        color: red;
    }
    .searchinput{
        position: absolute;
        bottom: -4px;
        left: -25px;
        width: 20px;
    }
    .el-pagination{
        margin-top: 10px;
    }
    .rowItem{
        position: relative;
    }


</style>
<style>
    /*.el-input__inner {*/
    /*height: 25px !important;*/
    /*}*/
    body, html {
        overflow: auto;
        margin: 0;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
    }
    .search_button{
        margin-top: 8px;
        text-align: center;
    }
    .el-popover__title{
        font-size: 14px;
    }
    .el-table .cell{
        min-height: 20px!important;
    }
    .el-table th>.cell{
        line-height: 50px;

    }
    .el-table td{
        padding: 10px 0!important;
    }
    /*::-webkit-scrollbar{*/
        /*width: 10px;*/
        /*background: #fff;*/
    /*}*/
</style>
