<template>
<div>
    <button v-on:click="show = !show">
        Toggle
    </button>
    <transition name="fade" :duration="3000">
        <p v-if="show">hello</p>
    </transition>


    <div v-for="(i,index) in arr " :key="index">
        <div>{{ i.sum }}</div>
        <button @click="add(index)">增加</button>
        <button @click="dec(index)">减少</button>
    </div>
    <div class="aaa">
        <div class="oooo"></div>
    </div>








</div>
</template>

<script>
    export default {
        name: "ex",
        data:function () {
            return {
                show: true,
                arr:[{id:1,sum:10},{id:2,sum:10},{id:3,sum:10}]
            }



        },
        methods:{
            add:function (index) {
                window.console.log(index)

                 this.arr[index].sum ++

            },
            dec:function (index) {
                if(this.arr[index].sum >0){
                    this.arr[index].sum = this.arr[index].sum -1
                }

            }
        }
    }
</script>

<style scoped lang="scss">
    .fade-enter-active  {
        transition: opacity 9s;
    }
    .fade-leave-active{
        /*transition: opacity 21s;*/
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
    .aaa{
        width: 400px;
        height: 400px;
        background: red;
        .oooo{
            width: 200px;
            height: 200px;
            background: forestgreen;
        }
    }

</style>

