<template>
    <div >
       <div @click="toImage">打印</div>
        <canvas id="canvas"></canvas>
        <div class="imageWrapper" ref="imageWrapper" id="imageWrapper" style="width: 500px;height: 500px">
            <img src="../assets/logo.png"  alt="" @load="aa" style="width: 50px;height: 50px">
        </div>
        <div style="width: 200px;height: 200px">
            <img class="real_pic" :src="dataURL"  style="width: 200px;height: 200px"/>
            <slot></slot>
        </div>
        <div @click="print">大航海</div>
        <div @click="install">jjj</div>
        <div @click="savecode">生成条形码</div>
        <div ref="pp">
            <svg id="barcode" ref="ppa"></svg>
        </div>

    </div>

</template>

<script>
    import JsBarcode from 'jsbarcode'
    import 'pdfmake/build/pdfmake.min'
    import 'pdfmake/build/vfs_fonts'
    import html2canvas from "html2canvas"
    import JsPDF from 'jspdf'
    export default {
        name: "printing",
        data() {
            return{
                dataURL:'',
              codeArr:[]
            }
        },
        methods:{
            install (Vue, options) {
                    var title = '1234';
                    html2canvas(document.querySelector('#imageWrapper'), {
                        allowTaint: true
                    }).then(function (canvas) {
                            let contentWidth = canvas.width
                            let contentHeight = canvas.height
                            let pageHeight = contentWidth / 592.28 * 841.89
                            let leftHeight = contentHeight
                            let position = 0
                            let imgWidth = 595.28
                            let imgHeight = 592.28 / contentWidth * contentHeight
                            let pageData = canvas.toDataURL('image/jpeg', 1.0)
                            let PDF = new JsPDF('', 'pt', 'a4')
                            if (leftHeight < pageHeight) {
                                PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
                            } else {
                                while (leftHeight > 0) {
                                    PDF.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
                                    leftHeight -= pageHeight
                                    position -= 841.89
                                    if (leftHeight > 0) {
                                        PDF.addPage()
                                    }
                                }
                            }
                            PDF.save(title + '.pdf')
                        }
                    )

            },
            aa(e){
                console.log(e)
            },
          savecode(){



            JsBarcode("#barcode", "1234", {
              format: "pharmacode",  //条形码的格式
              lineColor: "#0aa",  //线条颜色
              width:4, //线宽
              height:40,  //条码高度
              displayValue: false //是否显示文字信息
            });
            this.$refs.ppa.removeAttribute('xmlns');
            console.log(this.$refs.pp.innerHTML)
            // $("#imgcode"+i+" #svg").removeAttr('xmlns');
            this.codeArr.push(this.$refs.pp.innerHTML);
            console.log(this.codeArr)

          },
            loadImage(url){
                let reader = new FileReader();     //html5读文件
                reader.readAsDataURL(url);
                // console.log(reader.readAsDataURL(url))
            },
            toImage() {
                html2canvas(this.$refs.imageWrapper,{
                    backgroundColor: null
                }).then((canvas) => {
                    let dataURL = canvas.toDataURL("image/png");
                    this.dataURL = dataURL;
                    // console.log(this.dataURL)
                });
            },
            ImageDataURL(urls) {//urls必須是字符串或字符串數組
                console.log(urls)
                this.completenum = 0;
                this.totalnum = 0;
                this.imgdata = new Array();
                this.emptyobj = new Object();
                this.oncomplete = function(){};
                this.getDataURL = function(url, index) {
                    var c = document.createElement("canvas");
                    // var c = document.getElementById('canvas')
                    var cxt = c.getContext("2d");
                    var img = new Image();
                    var dataurl;
                    var p;
                    p = this;
                    img.src = url;
                    console.log(img);
                    img.onload = function() {
                        console.log(img);
                        var i;
                        c.width= img.width;
                        c.height= img.height;
                        cxt.fillStyle = "#fff";
                        cxt.fillRect(0, 0, c.width, c.height);
                        cxt.drawImage(img, 0, 0, c.width, c.height);
                        p.imgdata[index] = c.toDataURL('image/jpeg');
                        console.log(p.imgdata[index])
                        for (i = 0; i < p.totalnum; ++i) {

                            if (p.imgdata[i] == null)break;
                        }
                        if (i == p.totalnum) {
                            p.oncomplete();
                        }
                    };
                    img.onerror = function(e) {
                        console.log(34,e)
                        p.imgdata[index] = p.emptyobj;
                        for (var i = 0; i < p.totalnum; ++i) {
                            if (p.imgdata[i] == null)break;
                        }
                        if (i == p.totalnum) {
                            p.oncomplete();
                        }
                    };
                }
                if (urls instanceof Array) {
                    this.totalnum = urls.length;
                    this.imgdata = new Array(this.totalnum);
                    console.log('op')
                    for (var key in urls) {
                        this.getDataURL(urls[key], key);
                    }
                } else {
                    this.imgdata = new Array(1);
                    this.totalnum = 1;
                    this.getDataURL(urls, 0);
                }
            },
            print(){

                pdfMake.fonts = {
                    文泉驿微米黑: {
                        normal: '文泉驿微米黑.ttf',
                        bold: '文泉驿微米黑.ttf',
                        italics: '文泉驿微米黑.ttf',
                        bolditalics: '文泉驿微米黑.ttf',
                    },
                    arialbd: {
                        normal: 'arialbd.ttf',
                        bold: 'arialbd.ttf',
                        italics: 'arialbd.ttf',
                        bolditalics: 'arialbd.ttf'
                    },
                }
                var content = {
                    content:
                        [
                            {
                                text: 'bne0876',
                                fontSize: 16,
                                rowSpan: 1,
                                margin: [80, 0, 0, 10],
                                style: ['bill_no', 'otherfont']
                            },
                            {text: '运单号:', fontSize: 15, colSpan: 1, margin: [12, -28, 0, 10]},
                            {text: '收件人：昂金189678978809', fontSize: 10, margin: [12, -6, 0, 0]},
                            {
                                      svg:this.codeArr[0],
                                      width: 100,
                                      height:30,
                                      margin: [80, 0, 0, 0]
                                  },
                        ],
                    compress: false,
                    styles: {

                        tableExample: {
                            noBorders: true,
                            cellBorder: 0,
                            tableCellPadding: 0
                        },
                        bill_no: {
                            alignment: 'left'

                        },
                        underline: {
                            decoration: 'underline',
                            decorationColor: '#000000',
                            color: '#ffffff'
                        },
                        bold: {
                            bold: true
                        },
                        otherfont: {
                            font: 'arialbd'
                        }

                    },
                    defaultStyle: {
                        font: '文泉驿微米黑'
                    },
                    pageBreakBefore: function (currentNode, followingNodesOnPage, nodesOnNextPage, previousNodesOnPage) {
                        //console.log(currentNode)
                        if (currentNode.headlineLevel == 1) {
                            return true

                        }
                        // return currentNode.headlineLevel === 1 && followingNodesOnPage.length === 0;
                    },
                    pageSize: {
                        width: ($("#choose_printing_methods").val() == 1) ? 283 : 283,
                        height: ($("#choose_printing_methods").val() == 1) ? 420 : 114
                    },

                    pageMargins: [0, 0, 0, 0]
                }
                pdfMake.createPdf(content).open();
            }



        },
        created() {
        }




    }
</script>

<style scoped>

